.landing-image-container {
    position: relative;
    top: -82px;
}

.landing-text {
    position: absolute;
    left: 30%;
    top: 30%;
}

.landing-text .big-text-container {
    font-size: 91px;
    font-weight: 700;
    color: white;
    font-family: 'Roboto Condensed', sans-serif;
    border-bottom: 5px solid orangered;
    line-height: 100px;
}


.stroke-text {

    color: white;
    -webkit-text-fill-color: transparent;
    /* Will override color (regardless of order) */
    -webkit-text-stroke-width: 2px;
    -webkit-text-stroke-color: white;
}

.landing-search-contianer {
    position: absolute;
    left: 22%;
    border-radius: 30px;
    /* overflow: hidden; */
    bottom: -50px;
}

.image {
    position: relative;
    width: 100%;
    overflow: hidden;
    border-radius: 30px;
}

.image-overlay .overlay-container {
    position: absolute;
    bottom: 0;
    padding-left: 25px;
    padding-top: 15px;
    width: 100%;
    color: white;
    font-size: 20px;
    z-index: 5;
}

.image-overlay .overlay-container::before {
    content: "";
    background: #ffff;
    height: 100%;
    width: 100%;
    z-index: 1;
    position: absolute;
    left: 0;
    bottom: -150px;
    z-index: -2;
    opacity: 0.5;
    transition: all 0.3s ease-out;
}

.image-overlay:hover .overlay-container {
    color: black;
}

.search-btn-container button {
    background: #3B5AFF;
    color: white;
    width: 100px;
}

.css-b62m3t-container {
    width: 230px;
    font-size: 13px;
}

.top-destination-container {
    margin-top: 100px;
    padding: 15px;
    background-color: white;
    margin-left: 87px;
    border-radius: 30px 0px 0px 30px;
    box-shadow: -6px 5px 7px 2px #00000040;
}

.top-des-heading {
    color: black !important;
    font-size: 40px;
    font-weight: 400;
}

.top-text-container {
    max-width: 450px;
    font-size: 13px;
    text-align: justify;
    color: #737272;
}

.see-more-btn {
    background-color: #3B5AFF;
    width: 130px;
    border-radius: 40px;
    margin: 0px auto;
    margin-top: 50px;
}

.see-more-btn button {
    color: white;
}

.destination-name-container,
.recommendation-name {
    font-size: 25px;
    margin-top: 12px;
    font-weight: bold;
    font-family: 'Righteous', cursive;

}

.recommendation-name {
    font-size: 22px;
    font-weight: 400;
}

.destination-text-container {
    font-size: 13px;
    text-align: justify;
    color: #282727;
    margin-top: 12px;

}

.top-destination-holder {
    width: 100%;
    overflow: scroll;
}

.page-heading {
    font-family: 'Righteous', cursive;
    font-size: 40px;
    font-weight: 400;
}

.top-destination-child {
    width: 320px;
}

.experience-name {
    font-family: 'Righteous', cursive;
    font-size: 16px;
    font-weight: 400;
    text-align: center;
    margin: 10px 0px;

}

.experience-price {
    text-align: center;
    color: #F00000;
    font-weight: 700;
    font-size: 16px;
}

.experience-country {
    text-align: center;
    margin-top: 10px;
    font-weight: 500;
    font-size: 16px;
    color: #4d4d4d;
}

.experience-child {
    width: 300px;
    margin: 20px;
}

.recommendation-child {
    width: 345px;
    overflow: hidden;
    background-color: white;
    box-shadow: 0px 2px 9px 4px #00000040;
    padding-bottom: 20px;

}

.destination-container {
    border-right: 2px solid black;
}

.recommendation-place {
    font-size: 18px;
    font-weight: 400;
}

.golden-text {
    color: #ffc800;
    font-size: 15px;
    font-weight: 500;
}

.recommendation-button-group {
    margin: 0px 40px;
}

.recommendation-button-group .heading {
    font-size: 19px;
}

.info-btn,
.redirect-btn {
    background: #193988;
    font-size: 12px;
    color: white;
    font-weight: 700;
}

.redirect-btn {
    background: #F1BD03;

}

.rating-overlay-container {
    position: absolute;
    bottom: 0;
    color: white;
    font-size: 12px;
    margin-bottom: 5px;
}

.rating-star span {
    font-size: 15px;
    color: #ffc800;
}


.rating-star .material-symbols-outlined {
    font-variation-settings:
        'FILL' 1,
        'wght' 400,
        'GRAD' 0,
        'opsz' 48
}

.feedback-text {
    font-size: 20px;
    font-weight: 400;
    line-height: 32px;
    text-align: justify;
}

.client-name {
    font-family: Righteous;
    font-size: 30px;
    font-weight: 400;
    line-height: 32px;
    letter-spacing: 0em;
    text-align: center;
    margin-top: 12px;
}

@media only screen and (min-width: 992) {}

@media only screen and (min-width: 768) {}

@media screen and (max-width: 600px) {
    .landing-text .big-text-container {
        font-size: 40px;
        font-weight: 700;
        color: white;
        font-family: 'Roboto Condensed', sans-serif;
        border-bottom: 5px solid orangered;
        line-height: 100px;
    }
}